import { Component, OnInit } from '@angular/core';
import { Renderer2, Inject } from '@angular/core';
import { DOCUMENT } from '@angular/platform-browser';

import {AuthService} from "../../services/auth.service";
import {Router} from "@angular/router";
import { ActivatedRoute } from "@angular/router";

declare var AddressFinder: any;

@Component({
  selector: 'app-create-student',
  templateUrl: './create-student.component.html',
  styleUrls: ['./create-student.component.css']
})
export class CreateStudentComponent implements OnInit {
  id: any;
  editMode : Boolean = false;
  //
  firstname: String;
  middlename: String;
  lastname: String;
  dateofbirth: String;
  gender: Boolean;
  logbookhours: Number;
  licensetype: String;
  licensenumber: String;
  licenseexpiry: String;
  homephone: String;
  mobilephone: String;
  email: String;
  address: String;
  suburb: String;
  notes: String;

  constructor(private renderer2: Renderer2,
              @Inject(DOCUMENT) private _document,
              private authService: AuthService,
              private router: Router,
              private route : ActivatedRoute
  ) {
    this.route.queryParams.subscribe(params => {
      this.id = params['id'];
      if (this.id !== undefined) {
        this.editMode = true;
        this.populateFields();
        console.log('EDIT_MODE_ACTIVE');
      }
    });
  }

  ngOnInit() {
    let script = document.createElement("script");
    script.src = "https://api.addressfinder.io/assets/v3/widget.js";
    script.async = true;
    script.onload = this.initAf;
    document.body.appendChild(script);
    console.log('Injected AddressFinder Script!');
  }

  initAf() {
    let widget = new AddressFinder.Widget(
      document.getElementById("address"),
      "9AEGNR3867HK4DFVXPLW",
      'AU', {
        "address_params": {
          "post_box": "0"
        }
      }
    );

    widget.on("result:select", function(fullAddress, metaData) {
      //(<HTMLInputElement>document.getElementById('suburb')).value = metaData.locality_name;
      document.getElementById('address').setAttribute('value', fullAddress);
      document.getElementById('suburb').setAttribute('value', metaData.locality_name);
    });
  }

  populateFields(){
    this.authService.getStudentById(this.id).subscribe(student => {
      this.firstname = student['firstname'];
      this.middlename = student['middlename'];
      this.lastname = student['lastname'];
      this.dateofbirth = student['dateofbirth'];
      this.gender = student['gender'];
      this.logbookhours = student['logbookhours'];
      this.licensetype = student['licensetype'];
      this.licensenumber = student['licensenumber'];
      this.licenseexpiry = student['licenseexpiry'];
      this.homephone = student['homephone'];
      this.mobilephone = student['mobilephone'];
      this.email = student['email'];
      document.getElementById('address').setAttribute('value', student['address']);
      document.getElementById('suburb').setAttribute('value', student['suburb']);
      this.notes = student['notes'];
    })
  }

  onCreateStudentSubmit(){
    const student = {
      firstname: this.firstname,
      middlename: this.middlename,
      lastname: this.lastname,
      dateofbirth: this.dateofbirth,
      gender: this.gender,
      logbookhours: this.logbookhours,
      licensetype: this.licensetype,
      licensenumber: this.licensenumber,
      licenseexpiry: this.licenseexpiry,
      homephone: this.homephone,
      mobilephone: this.mobilephone,
      email: this.email,
      address: document.getElementById('address').getAttribute('value'),
      suburb: document.getElementById('suburb').getAttribute('value'),
      notes: this.notes,
      id : undefined
    };

    if (this.editMode) {
      // Inject _id from params, needed only for edit mode in order to delete old document
      student.id = this.id;
      this.authService.updateStudentById(student).subscribe(data => {
        if(data['success'] == true){
          console.log('Update success!');
          this.router.navigate(['/']);
        } else {
          console.log('Update failure!');
          this.router.navigate(['/']);
        }
      })
    } else {
      // Create Student
      this.authService.createStudent(student).subscribe(data => {
        if(data['success'] == true){
          console.log('Registration success!');
          this.router.navigate(['/']);
        } else {
          console.log('Registration failure!');
          this.router.navigate(['/']);
        }
      })
    }
  }

}
